export default (sequelize, Sequelize) => {
  const AlumniArticles = sequelize.define("alumni_article", {
    name_en: {
      type: Sequelize.STRING,
    },
    name_th: {
      type: Sequelize.STRING,
    },
    cover_img_url: {
      type: Sequelize.TEXT,
    },
    content_en: {
      type: Sequelize.TEXT,
    },
    content_th: {
      type: Sequelize.TEXT,
    },
    release_date: {
      type: Sequelize.DATEONLY,
    },
  });
  return AlumniArticles;
};
